





















































































































































import Const from '@/classes/const';
import ShipMaster from '@/classes/fleet/shipMaster';
import ShipValidation from '@/classes/fleet/shipValidation';
import ItemMaster from '@/classes/item/itemMaster';
import SiteSetting from '@/classes/siteSetting';
import Vue from 'vue';

export default Vue.extend({
  name: 'ShipMasterList',
  components: {},
  data: () => ({
    rangeText: ['', '短', '中', '長', '超長', '超長+'],
    keyword: '',
    isFinalOnly: true,
    daihatsuOK: false,
    daihatsuNG: false,
    tankOK: false,
    tankNG: false,
    types: [] as { text: string; value: number }[],
    selectedTypes: [] as number[],
    headers: [
      {
        text: '',
        sortable: false,
        value: 'id',
      },
      {
        text: '艦娘名',
        value: 'name',
      },
      {
        text: '耐久',
        filterable: false,
        align: 'end',
        value: 'hp',
      },
      {
        text: '火力',
        filterable: false,
        align: 'end',
        value: 'fire',
      },
      {
        text: '装甲',
        filterable: false,
        align: 'end',
        value: 'armor',
      },
      {
        text: '雷装',
        filterable: false,
        align: 'end',
        value: 'torpedo',
      },
      {
        text: '夜戦',
        filterable: false,
        align: 'end',
        value: 'night',
      },
      {
        text: '回避',
        filterable: false,
        align: 'end',
        value: 'maxAvoid',
      },
      {
        text: '対空',
        filterable: false,
        align: 'end',
        value: 'antiAir',
      },
      {
        text: '対潜',
        filterable: false,
        align: 'end',
        value: 'maxAsw',
      },
      {
        text: '速力',
        filterable: false,
        align: 'end',
        value: 'speed',
      },
      {
        text: '索敵',
        filterable: false,
        align: 'end',
        value: 'maxScout',
      },
      {
        text: '射程',
        filterable: false,
        align: 'end',
        value: 'range',
      },
      {
        text: '運',
        filterable: false,
        align: 'end',
        value: 'luck',
      },
      {
        text: '燃料',
        filterable: false,
        align: 'end',
        value: 'fuel',
      },
      {
        text: '弾薬',
        filterable: false,
        align: 'end',
        value: 'ammo',
      },
    ],
    ships: [] as ShipMaster[],
  }),
  mounted() {
    // 艦種セレクト初期化
    const masters = Const.SHIP_TYPES_ALT;
    this.types = [];
    this.selectedTypes = [];
    for (let i = 0; i < masters.length; i += 1) {
      this.types.push({ text: masters[i].text, value: i });
      if (masters[i].text === '駆逐') {
        this.selectedTypes.push(i);
      }
    }
    this.initShips();
  },
  computed: {
    completed() {
      return this.$store.getters.getCompletedAll;
    },
    needTrans(): boolean {
      const setting = this.$store.state.siteSetting as SiteSetting;
      return this.$i18n.locale !== 'ja' && !setting.nameIsNotTranslate;
    },
    selectedAllType(): boolean {
      return this.selectedTypes.length === this.types.length;
    },
    selectedSomeType(): boolean {
      return this.selectedTypes.length > 0 && !this.selectedAllType;
    },
    translatedShipTypes(): { text: string }[] {
      const array = [];
      for (let i = 0; i < this.types.length; i += 1) {
        const data = this.types[i];
        array.push({ text: `${this.$t(`SType.${data.text}`)}`, value: data.value });
      }
      return array;
    },
    icon(): string {
      if (this.selectedAllType) return 'mdi-close-box';
      if (this.selectedSomeType) return 'mdi-minus-box';
      return 'mdi-checkbox-blank-outline';
    },
    getShipName() {
      return (ship: ShipMaster) => {
        if (this.needTrans) {
          const array = ShipMaster.getSuffix(ship);
          return `${array.map((v) => (v ? `${this.$t(v)}` : '')).join('')}`;
        }
        return ship.name ? ship.name : '';
      };
    },
    speedText() {
      return (value: number) => {
        if (value <= 5) {
          return `${this.$t('Fleet.低速')}`;
        }
        if (value <= 10) {
          return `${this.$t('Fleet.高速')}`;
        }
        if (value <= 15) {
          return `${this.$t('Fleet.高速+')}`;
        }
        return `${this.$t('Fleet.最速')}`;
      };
    },
  },
  watch: {
    completed(value) {
      if (value) {
        this.initShips();
      }
    },
  },
  methods: {
    initShips() {
      const all = this.$store.state.ships as ShipMaster[];
      let ships = [];
      const types = Const.SHIP_TYPES_ALT.filter((v, i) => this.selectedTypes.includes(i))
        .map((v) => v.types)
        .flat();

      for (let i = 0; i < all.length; i += 1) {
        const ship = all[i];

        if (!types.includes(ship.type)) {
          // 艦種フィルタ
          continue;
        }

        if (this.isFinalOnly && !ship.isFinal) {
          // 最終改造フィルタ
          continue;
        }

        ships.push(ship);
      }

      const isValid = ShipValidation.isValidItem;

      if (this.daihatsuOK) {
        // 大発搭載可能
        const daihatsu = (this.$store.state.items as ItemMaster[]).find((v) => v.id === 68);
        if (daihatsu) {
          ships = ships.filter((v) => isValid(v, daihatsu));
        }
      } else if (this.daihatsuNG) {
        // 大発搭載不可
        const daihatsu = (this.$store.state.items as ItemMaster[]).find((v) => v.id === 68);
        if (daihatsu) {
          ships = ships.filter((v) => !isValid(v, daihatsu));
        }
      }

      if (this.tankOK) {
        // 内火艇搭載可能
        const tank = (this.$store.state.items as ItemMaster[]).find((v) => v.id === 167);
        if (tank) {
          ships = ships.filter((v) => isValid(v, tank));
        }
      } else if (this.tankNG) {
        // 内火艇搭載不可
        const tank = (this.$store.state.items as ItemMaster[]).find((v) => v.id === 167);
        if (tank) {
          ships = ships.filter((v) => !isValid(v, tank));
        }
      }

      this.ships = ships.sort((a, b) => a.sort - b.sort);
    },
    toggleDaihatsuFilter() {
      if (this.daihatsuOK) {
        this.daihatsuOK = false;
        this.daihatsuNG = true;
      } else if (this.daihatsuNG) {
        this.daihatsuNG = false;
      } else {
        this.daihatsuOK = true;
      }
      this.initShips();
    },
    toggleTankFilter() {
      if (this.tankOK) {
        this.tankOK = false;
        this.tankNG = true;
      } else if (this.tankNG) {
        this.tankNG = false;
      } else {
        this.tankOK = true;
      }
      this.initShips();
    },
    toggleAllType() {
      this.$nextTick(() => {
        if (this.selectedAllType) {
          this.selectedTypes = [];
        } else {
          this.selectedTypes = this.types.map((v) => v.value).slice();
        }
        this.initShips();
      });
    },
  },
});
